  line-height: 12px;
  outline: none;
}
@media (max-width: 767px) {
  .footer__strictures {
    align-self: flex-start;
    margin: 0 0 0 5px;
  }
}

.menu ul {
  display: flex;
  flex-wrap: wrap;
  max-width: 710px;
  margin: 1px 63px 0 0;
  padding: 0;
  list-style-type: none;
  gap: 5px 20px;
}
@media (max-width: 1199px) {
  .menu ul {
    margin: 0 0 10px;
    gap: 5px 9px;
  }
}
@media (max-width: 767px) {
  .menu ul {
    display: block;
    width: 100%;
    margin: 0;
    column-count: 2;
    gap: 0 50px;
  }
}
@media (max-width: 767px) {
  .menu li {
    margin: 0 0 2px;
  }
}
.menu a {
  position: relative;
  display: inline-block;
  padding: 5px;
  line-height: 16px;
  border: 1px solid transparent;
  outline: none;
  transition: border 0.3s ease;
}
@media (max-width: 1199px) {
  .menu a {
    font-size: 14px;
    line-height: 14px;
  }
}
.menu a:hover {
  border-bottom: 1px solid #2d383f;
}
.menu a:focus {
  border: 1px solid #2d383f;
}
.menu a:focus:active {
  border-color: transparent;
}
.menu a:active {
  border-color: transparent;
}
.menu a:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.menu a::before {
  content: "";
  position: absolute;
  transition: background-color 0.3s ease;
}

