.phone {
  a {
    font-weight: 600;
    font-size: 18px;
    line-height: 18px;
    color: inherit;

    outline: none;

    transition: color $trans-default;

    @include link-states;

    @include vp-1199 {
      z-index: 1;

      font-size: 16px;
      line-height: 16px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 14px;
    }
  }
}